.login {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  overflow: hidden;
  color: rgb(201, 179, 179);
  background-color: white;
  &__login-box {
    display: flex;
    align-items: center;
    position: relative;
  }
  &__login-span {
    font-size: 2.5rem;
    font-weight: 400;
    margin-left: 7%;
    position: relative;
    &::before {
      content: "_";
      font-weight: bold;
    }
  }
  &__boubles {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: $main-color;
    &--one {
      position: absolute;
      right: 55px;
      // box-shadow: -2px -3px 16px 0px $main-color;
    }
    &--two {
      width: 80px;
      height: 80px;
      position: absolute;
      right: -40px;
      // box-shadow: -3px 0 10px 0px $main-color;
    }
    &--three {
      width: 80px;
      height: 80px;
      position: absolute;
      left: -40px;
      //  box-shadow: -3px 0 10px 0px $main-color;
    }
    &--four {
      position: absolute;
      left: 55px;
      //  box-shadow: -2px -3px 16px 0px $main-color;
    }
  }
  &__signUp-box {
    display: flex;
    align-items: center;
    position: relative;
    justify-content: flex-end;
  }
  &__signUp-span {
    font-size: 2.5rem;
    font-weight: 400;
    margin-right: 6%;
    position: relative;
    &::after {
      content: "_";
      font-weight: bold;
    }
  }
  &__form-wrapper {
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  &__inputs-box {
    width: 92%;
    height: 30%;
    margin-bottom: 30px;
    //box-shadow: 0 0 40px -10px rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 8px;
    background-color: #fff;
  }
  &__username,
  &__password {
    width: 80%;
    padding: 5px 0 5px 30px;
    border: none;
    border-bottom: 1px solid rgb(201, 179, 179);
    font-size: 1.5rem;
    outline: none;
    &::placeholder {
      color: rgb(201, 179, 179);
    }
  }
  &__log-button-wrapper {
    width: 100%;
    position: relative;
    margin-top: 15px;
  }
  &__button {
    background-color: $main-color;
    width: 80%;
    border: none;
    color: white;
    padding: 10px 0;
    border-radius: 20px;
    font-size: 1.2rem;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    &--google {
      background-color: white;
      margin: 60px 0 10px 0;
      height: 45px;
      border-radius: 3px;
      font-size: 1rem;
      padding: 0;
      color: black;
      box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
      justify-content: flex-start;
    }
    &--facebook {
      background-color: #3b5998;
      box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
      border-radius: 3px;
      height: 45px;
      font-size: 1rem;
      padding: 0;
      justify-content: flex-start;
    }
    &--fb-icon {
      width: 20px;
      margin: 0 20px 0 15px;
    }
    &--google-icon {
      width: 25px;
      margin: 0 14px 0 15px;
    }
  }
}
